
import wx from 'weixin-js-sdk'
import {mapActions,mapGetters}from 'vuex'
import adrBtn from '../components/addressBtn'
import Group from 'vux/src/components/group/index.vue'
import XInput from 'vux/src/components/x-input/index.vue'
import Popup from 'vux/src/components/popup/index.vue'
import Picker from 'vux/src/components/picker/index.vue'
import XTextarea from 'vux/src/components/x-textarea/index.vue'

export default {
name: "plus-address-view",
data(){
return{
showPick:false,
noFixed:false,
picked:[''],
quyu:[],
jd:'yiji',
show8:false,
detail:{
postalAddressData:{}
},
name:'张三',
mobile:'18913351891',
area: ['北京市'],
address:'',

addressData:[{name: "北京市", value: "110000"},{name: "上海市", value: "120000"}]
}
},
mounted(){
if(this.$route.query.id){
document.title = '编辑收货地址'
}
else{
document.title = '新增收货地址'
}
window.onresize = this.wreize
},
computed:{
...mapGetters({
ssdq:'getssdq'
}),
adrData(){
return [this.ssdq['yiji']]
},
adrData1(){
return [this.ssdq['erji']]
},
adrData2(){
return [this.ssdq['sanji']]
}
},
created(){
if(this.$route.query.id){
this.addressDetail({this:this,data:{contactMechId:this.$route.query.id}})
}
},
methods:{
...mapActions(['addressDetail','addressAdd','addressDel','addressUpdate','getAdStt','clearSsq']),
cancel(){
if(this.jd === 'yiji'){
this.showPick = false
}
},
onFocus (val, $event) {
$event.target.blur()
},
choseWhy(k){
},
goCe(i,p){
console.log(i)
console.log(p)
console.log(this.quyu)
console.log(this.picked[0])
if(i){
if(this.jd == 'yiji'){
this.quyu = []
}
this.quyu.push({name:this.$refs[p].getNameValues(),id:this.picked[0]})
this.jd = i
this.getAdStt({type:this.jd,data:{countryGeoId:this.picked[0]},this:this})
}
else{
this.jd = 'yiji'
this.quyu.push({name:this.$refs[p].getNameValues(),id:this.picked[0]})
let ls = this.quyu.map(v => v.name)
this.$set(this.detail,'ssq',ls.join(' '))
this.showPick = false
this.clearSsq()
}
console.log(i)
console.log(this.quyu)
},
wreize(){
let client_h =  document.documentElement.clientHeight;
if(client_h < 450){
this.noFixed = true
}
else{
this.noFixed = false
}
},
back(){
if(this.jd == 'sanji'){
this.jd = 'erji'
}
else if(this.jd == 'erji'){
this.jd = 'yiji'
}
else{
this.show8 = false
}
},
cz(){
this.show8 = false
this.jd = 'yiji'
console.log(this.quyu)
},
slected(i){
switch (this.jd){
case 'yiji':
this.jd = 'erji'
this.quyu =[]
this.quyu.push(i)

break
case 'erji':
this.jd = 'sanji'
this.quyu.splice(1,1,i)
break
default:
this.quyu.splice(2,1,i)
this.jd = 'yiji'
}
let ls = this.quyu.map(v => v.name)
this.$set(this.detail,'ssq',ls.join(' '))
this.getAdStt({type:this.jd,data:{countryGeoId:i.id},this:this})
return
},
coseAdre(){

this.jd = 'yiji'
this.picked = ['']
this.quyu =[]
this.showPick = true
this.getAdStt({type:'yiji',data:{countryGeoId:'CHN'},this:this})
},
del(){
this.addressDel({this:this,data:{contactMechId:this.$route.query.id}})
},
save(){
let test = /^134[0-8]\d{7}$|^13[^4]\d{8}$|^14[5-9]\d{8}$|^15[^4]\d{8}$|^16[6]\d{8}$|^17[0-8]\d{8}$|^18[\d]{9}$|^19[8,9]\d{8}$/
if(!this.detail.postalAddressData.attnName||!this.detail.postalAddressData.mobilePhone||!this.detail.postalAddressData.address1){
this.showTost('请完善收货地址')
return
}
if(this.detail.postalAddressData.attnName.length<2){
this.showTost('请完善收货人姓名')
return
}
if(!test.test(this.detail.postalAddressData.mobilePhone)){
this.showTost('手机号码格式不正确')
return
}
if(this.detail.postalAddressData.address1.length <5){
this.showTost('请输入具体地址')
return
}
let obj={
countyGeoId:'',
stateProvinceGeoId:'',
city:''
}
if(this.quyu[0]){
obj.stateProvinceGeoId = this.quyu[0].id
}
else{
obj.stateProvinceGeoId =this.detail.postalAddressData.stateProvinceGeoId
}
if(this.quyu[1]){
obj.city = this.quyu[1].id
}
else{
obj.city=this.detail.postalAddressData.city
}
if(this.quyu[2]){
obj.countyGeoId = this.quyu[2].id
}
else{
obj.countyGeoId =this.detail.postalAddressData.countyGeoId
}
this.showLoading()
if(this.$route.query.id){
this.addressUpdate({
data:{
address:this.detail.postalAddressData.address1,
tel:this.detail.postalAddressData.mobilePhone,
name:this.detail.postalAddressData.attnName,
is_def:'Y',
contactMechId:this.$route.query.id,
...obj
},
this:this})
}
else{
this.addressAdd({
data:{
address:this.detail.postalAddressData.address1,
tel:this.detail.postalAddressData.mobilePhone,
name:this.detail.postalAddressData.attnName,
is_def:'Y',
...obj
},
this:this})
}
},
setMehod(){
this.hideLoading()
let _this = this
if(this.$route.query.sign){//vue页面过来选地址
}
else if(this.$route.query.reBack){//小程序页面过来



wx.miniProgram.navigateBack({
delta:1
})
}
else{
this.$router.go(-1)

}
},
coprData(data){
data.countyName = data.countyName||''
data.cityName = data.cityName||''
data.provinceName = data.provinceName|| ''
data.ssq= data.provinceName+ ' ' + data.cityName + ' ' + data.countyName
this.detail = Object.assign({},data)
}
},
components:{
Group,XInput,adrBtn,Popup ,Picker , XTextarea
}
}



